<template><section><h1>Loading 加载</h1>
<hr>
<h3>全局加载</h3>
<p>使用<code v-pre>type='global'</code>属性来定义 全局加载。</p>
<div class="demo-block">
   <p-button type="blue" @click="showIt()">全局加载</p-button>
   <p-loading type="global" :isShow="showGlobal"></p-loading>
   
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><p-loading type="global" isshow></p-loading>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">p-loading</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"global"</span> <span class="hljs-attr">isShow</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p-loading</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>局部加载</h3>
<p>使用<code v-pre>type='part'</code>属性来定义局部加载。</p>
<div class="demo-block">
<p-button type="blue" @click="showP()">局部加载</p-button>
   <p-loading type="part" :isShow="showPart" showText="自定义文字..."></p-loading>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><p-loading type="part" isshow showtext="自定义文字..."></p-loading>

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">p-loading</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"part"</span>  <span class="hljs-attr">isShow</span> <span class="hljs-attr">showText</span>=<span class="hljs-string">"自定义文字..."</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p-loading</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>global/part</td>
<td>—</td>
</tr>
<tr>
<td>isShow</td>
<td>是否显示加载</td>
<td>Boolean</td>
<td>true/false</td>
<td>false</td>
</tr>
</tbody>
</table>
</section></template>

<script>
   export default {
	 data () {
		return {
			showGlobal: false,
			showPart: false
		} 
	 },
	 methods: {
		 showIt(e) {
			 let _this = this
			 _this.showGlobal = true
			 setTimeout(function(){
				_this.showGlobal = false
			 }, 3000)
		 },
		  showP() {
		 			 let _this = this
		 			 _this.showPart = true
		 			 setTimeout(function(){
		 				_this.showPart = false
		 			 }, 3000)
		 }
	 }  
   }
   </script>


