<template><section><h1>notify 提示</h1>
<hr>
<h3>成功失败提示</h3>
<p>PersagyUI注册了$notify全局方法，使用<code v-pre>type'</code>属性来区分提示类型，默认显示时长2s,动画时长是0.5s。</p>
<div class="demo-block">
   <p-button type="blue" @click="success()">成功提示</p-button>
   <p-button type="red" @click="error()">失败提示</p-button>
   
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><p-button type="blue" @click="success()">成功提示</p-button>
  <p-button type="red" @click="error()">失败提示</p-button>
  

</body></html></div>
                        <div slot="highlight"><pre v-pre><code v-pre class="language-html">
  <span class="hljs-tag">&lt;<span class="hljs-name">p-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"blue"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"success()"</span>&gt;</span>成功提示<span class="hljs-tag">&lt;/<span class="hljs-name">p-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p-button</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"red"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"error()"</span>&gt;</span>失败提示<span class="hljs-tag">&lt;/<span class="hljs-name">p-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  	 data () {
  		<span class="hljs-keyword">return</span> {
  		} 
  	 },
  	 <span class="hljs-attr">methods</span>: {
  		 success() {
  			 <span class="hljs-keyword">this</span>.$pnotify.success(<span class="hljs-string">'成功啦！'</span>)
  		},
  		  error(){
  			  <span class="hljs-keyword">this</span>.$pnotify.error(<span class="hljs-string">'失败啦！'</span>)
  		  }
  }
  }
  </span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>类型</td>
<td>string</td>
<td>success/error</td>
<td>—</td>
</tr>
<tr>
<td>text</td>
<td>提示文字</td>
<td>string</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
   export default {
	 data () {
		return {
		} 
	 },
	 methods: {
		 success() {
			 this.$pnotify.success('成功啦！')
		},
		  error(){
			  this.$pnotify.error('失败啦！')
		  }
   }
   }
   </script>


